
export const formatNumber = (num) => {
  return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ');
}

/**
 * 
 * @param {*} dateStr - —Å—Ç—Ä–æ–∫–∞ –≤–∏–¥–∞ —á–∞—Å—ã:–º–∏–Ω—É—Ç—ã
 */
export const statisticTimeFormat = (dateStr) => {
  if (dateStr) {
    const dateStrArr = dateStr.split(':')
    return {
      hours: +dateStrArr[0],
      minutes: +dateStrArr[1]
    }
  }
  return null
}



