import { defineComponent } from '@vue/composition-api'
import StatisicChart from '@/components/statistic-chart/statistic-chart.vue'
import Stub from '@/components/stub/stub.vue'
import { formatNumber, statisticTimeFormat } from '@/components/composition/format-utils'

/**
 * dataToShow –ø–æ –∑–∞–ø—Ä–æ—Å—É getReport –∏–º–µ–µ—Ç —Å–ª–µ–¥—É—é—â—É—é —Å—Ç—Ä—É–∫—Ç—É—Ä—É
 * {
 *   // intervals - –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ { start: String, end: String, value: string }
 *   // timeUnit - –ø–æ —Å–ø–µ–∫–µ https://www.chartjs.org/docs/latest/axes/cartesian/time.html
 *   availableTime: {
 *     intervals: [],
 *     // —á–∞—Å—ã:–º–∏–Ω—É—Ç—ã
 *     totalValue: '0:0',
 *     timeUnit: 'week'
 *   },
 *   availableTimePercent: {
 *     intervals: [],
 *     // –æ—Ç–ª–∏—á–∏–µ –æ—Ç –≤—Ä–µ–º–µ–Ω–∏ –≤ —Ä–∞–∑–¥–µ–ª–∏—Ç–µ–ª–µ
 *     totalValue: '0.0',
 *     timeUnit: 'week'
 *   },
 *   problemTime: {
 *     intervals: [],
 *     // —á–∞—Å—ã:–º–∏–Ω—É—Ç—ã
 *     totalValue: '0:0',
 *     timeUnit: 'week'
 *   },
 *   notWorkingCameraCount: 0,
 *   totalTime: '0:0'
 * }
 */



export default defineComponent({
  name: 'Reports',
  components: {
    StatisicChart,
    Stub
  },
  setup() {
    const fieldsStatisticCommon = ['totalTime', 'notWorkingCameraCount']
    const filedsChart = ['availableTime', 'availableTimePercent', 'problemTime']
    return {
      fieldsStatisticCommon,
      filedsChart,
      formatNumber,
      statisticTimeFormat
    }
  },
  data() {
    return {
      dataToShow: window.dataToShow,
      reportSettings: window.reportSettings
    }
  },
  computed: {
    totalTimeView() {
      if (this.dataToShow) {
        const result = this.statisticTimeFormat(this.dataToShow.totalTime)
        result.hours = this.formatNumber(result.hours)
        return result
      }
    },
    reportSettingsShow() {
      if (this.reportSettings) {
        const result = { ...this.reportSettings }
        delete result.dateReport
        return result
      }
      return null
    }
  }
})