import { defineComponent } from '@vue/composition-api'
import { Line, Bar, mixins } from 'vue-chartjs'
import { useI18n } from 'vue-i18n-composable/dist/index.js'
import FixFluidColGrid from '@/components/fix-fluid-col-grid/fix-fluid-col-grid.vue'
import {formatNumber, statisticTimeFormat } from '@/components/composition/format-utils'

const createChart = extenedChart => ({
  extends: extenedChart,
  mixins: [mixins.reactiveProp],
  props: ['options'],
  mounted () {
    this.renderChart(this.chartData, this.options)
  }
})

export default defineComponent({
  name: 'StatisticChart',
  props: {
    typeView: {
      type: String,
      default: 'Line' // Line | Bar
    },
    type: {
      type: String,
      default: 'time' // time | percent
    },
    dataChart: {
      /**
       * intervals: [], –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ { start: String, end: String, value: string }
       * totalValue: '0:0' - time –∏–ª–∏ '0.0' - percent
       * timeUnit - –ø–æ —Å–ø–µ–∫–µ https://www.chartjs.org/docs/latest/axes/cartesian/time.html
       */
      type: Object,
      required: true
    },
    title: {
      type: String,
      default: ''
    }
  },
  components: {
    BarChart: createChart(Bar),
    LineChart: createChart(Line),
    FixFluidColGrid
  },
  setup(props, context) {
    const i18n = useI18n()
    const chartOptions = {
      animation: false,
      tooltips: {
        enabled: true,
        callbacks: {
          title(tooltipItem, data) {
            const item = data.datasets[0].data[tooltipItem[0].index]
            if (props.type === 'time') {
              const { hours, minutes } = item
              return i18n.t('chart.tootip', { hours: formatNumber(hours), minutes })
            }
            return `${item.y}%`
          },
          label() {
            return ''
          }
        },
        backgroundColor: '#384667',
        titleFontSize: 14,
        titleFontStyle: 'normal',
        titleMarginBottom: 0
      },
      legend: {
        display: false
      },
      scales: {
        xAxes: [{
          ticks: {
            fontSize: 14,
            fontColor: '#98A2BD',
            source: 'data',
            callback: function(value) {
              const xAxesTimeFormat = props.dataChart.timeUnit === 'hour' ? 'HH:mm' : 
                i18n.locale.value === 'ru' ? 'DD.MM' : 'MM/DD'
              return context.root.$moment(value).format(xAxesTimeFormat)
            }
          },
          gridLines: {
            zeroLineColor: '#FFFFFF',
            color: 'rgba(91,102,131, .2)'
          }
        }],
        yAxes: [{
          ticks: {
            fontSize: 14,
            fontColor: '#98A2BD',
            beginAtZero: true,
            callback: function(value) {
              return formatNumber(value)
            }
          },
          gridLines: {
            zeroLineColor: '#FFFFFF',
            color: 'rgba(91,102,131, .2)'
          }
        }]
      },
      responsive: true,
      maintainAspectRatio: false
    }

    return {
      chartOptions,
      formatNumber,
      statisticTimeFormat
    }
  },
  computed: {
    dataChartMap() {
      const dataChartMap = this.dataChart.intervals.map(item => {
        const resultObj = {
          /**
           * –∑–¥–µ—Å—å –¥–∞—Ç–∞ –∏ —Ñ–æ—Ä–º–∞—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ –µ–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –≤ –∫–æ–ª–±–µ–∫–µ,
           * –∏–Ω–∞—á–µ –º–æ–≥—É—Ç –±—ã—Ç—å –æ–¥–∏–Ω–∞–∫–æ–≤—ã–µ —Ç–æ—á–∫–∏ –Ω–∞–ø—Ä–∏–º–µ—Ä 01.05.2020 - 01.05.2021
          */
          x: item.start
        }
        if (this.type === 'time') {
          const { hours, minutes } = this.statisticTimeFormat(item.value)
          resultObj.y = hours
          resultObj.hours = hours
          resultObj.minutes = minutes
        } else {
          resultObj.y = +item.value
        }
        return resultObj
      })

      const datasets = this.typeView === 'Line' ? [
        {
          data: dataChartMap,
          fill: false,
          borderColor: '#D7491C',
          pointRadius: 4,
          pointBackgroundColor: '#FFFFFF',
          pointBorderWidth: 0
        }
      ] : [
        {
          data: dataChartMap,
          backgroundColor: '#51ACFF',
          barThickness: 8
        }
      ]


      return {
        datasets,
        labels: dataChartMap.map(item => item.x),
        borderColor: 'rgb(75, 192, 192)',
        lineTension: 0.1
      }
    },
    totalValueCmp() {
      if (this.type === 'time') {
        const result = this.statisticTimeFormat(this.dataChart.totalValue)
        result.hours = this.formatNumber(result.hours)
        return result
      }
      return this.dataChart.totalValue
    },
    currentLocale() {
      return this.$i18n.locale
    }
  }
})