import { defineComponent } from '@vue/composition-api'
import EmptySearchStub from '@/assets/svg/stubs/empty-search-stub.svg?inline'
import NoConnectionStub from '@/assets/svg/stubs/no-connection-stub.svg?inline'

export default defineComponent({
  name: 'Stub',
  props: {
    type: {
      type: String,
      default: 'emptySearch' // emptySearch | noConnection
    }
  },
  components: {
    EmptySearchStub,
    NoConnectionStub
  },
  computed: {
    iconCmpStr() {
      switch(this.type) {
        case 'emptySearch':
          return 'EmptySearchStub'
        case 'noConnection':
          return 'NoConnectionStub'
      }
    }
  },
  methods: {
    refreshPage() {
      location.reload()
    }
  }
})